import { normalize } from 'normalizr';
import { conceptSchema } from '../schema/conceptSchema';
const axios = require('axios');
export function flushConceptState() {
    return dispatch => {
        dispatch({
            type: "FLUSH_CONCEPT",
        })
    }
}
export function setConceptPageNo(pageNo) {
    return dispatch => {
        dispatch({
            type: 'SET_PAGE_NO',
            payload: pageNo
        })
    }
}
export function getAllPerspectivesByConcept(conceptId, page_no) {
    let conceptPage = {
        conceptId,
        page_no,
    }
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/concepts/perspective`,{conceptPage})
            .then(data => {
                const { responseType } = data.data;
                if (responseType == "success") {
                    dispatch({
                        type: 'SET_DETAILED_PERSPECTIVE',
                        payload: data.data.perspectives
                    })
                }
            })
    }
}
export function getConceptPerspectivesByKeywords(keywordId) {
    return dispatch => {
        axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/keywords/perspective/${keywordId}`)
            .then(data => {
                const { responseType } = data.data;
                if (responseType == "success") {
                    dispatch({
                        type: 'SET_DETAILED_PERSPECTIVE',
                        payload: data.data.data.Perspectives
                    })
                }

            })
    }
}
export function getConceptPerspectivesByAuthor(author_lastName) {
    return dispatch => {
        axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/authors/perspective/${author_lastName}`)
            .then(data => {
                const { responseType } = data.data;
                if (responseType == "success") {
                    dispatch({
                        type: 'SET_DETAILED_PERSPECTIVE',
                        payload: data.data.perspectives
                    })
                }

            })
            .catch(err => {
                console.log(err)
            })
    }
}
export function increasePerspectiveLike(id) {
    let like = {};
    like.type = "increase";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = true;
                dispatch({
                    type: 'SET_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}
export function decreasePerspectiveLike(id) {
    let like = {};
    like.type = "decrease";
    like.id = id;
    return dispatch => {
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/perspective/like`, { like })
            .then(res => {
                res.data.perspective.isLike = false;
                dispatch({
                    type: 'SET_PERSPECTIVE_LIKE',
                    payload: res.data.perspective
                })
            })
    }
}
export function sortByAuthorLast(sortBy) {
    return dispatch => {
        dispatch({
            type: "sortByAuthorLast",
            payload: sortBy
        })
    }
}
export function showAllPerspective(value) {
    return dispatch => {
        dispatch({
            type: "SHOW_ALL_PERSPECTIVE",
            payload: value
        })
    }
}
export function sortByKeyword(keyword) {
    return dispatch => {
        dispatch({
            type: "sortByKeyword",
            payload: keyword
        })
    }
}
export function getConceptFromApi(concept) {
    let obj = {}
    obj.Conceptobj = concept
    return dispatch => {
        dispatch({
            type: "REQUEST_STARTED"
        })
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/concepts/filter?relationships=true`, obj).then(data => {
            const { responseType } = data.data;
            if (responseType == 'success') {
                const conceptDetailsFromApi = data.data.Perspectives.perspectivesRelations;
                const normalizedData = normalize(conceptDetailsFromApi, conceptSchema);
                normalizedData.entities.perspectivesDetail = data.data.Perspectives.perspectivesDetail;
                console.log(normalizedData);
                dispatch({
                    type: "REQUEST_SUCCESSFUL",
                    payload: conceptDetailsFromApi
                });
                dispatch({
                    type: 'setPerspectiveIds',
                    payload: normalizedData.entities.concepts[normalizedData.result].Perspectives
                });
                dispatch({
                    type: "SET_CONCEPT_DETAILS",
                    payload: normalizedData.entities
                });
            }
            else {
                dispatch({
                    type: "SERVER_ERROR"
                })
            }
        }).catch(error => {
            dispatch({
                type: "NETWORK_ERROR"
            })
            console.log('Error While fetching concept details from api', error);
        })
    }
}
export function getListRelatedByConceptId(concept) {
    let obj = {}
    obj.Conceptobj = concept
    return dispatch => {
        dispatch({
            type: "LIST_RELATED_LOADING"
        })
        axios.post(`${process.env.REACT_APP_SERVER}/api/rest/v1/concepts/filter?relationships=true&withListAndRelatedOnly=true&fbclid=IwAR07GYuFbZ9CIrkYUy-0gMPOQrk-aSlLSKP-w0TP1FV5noOp1T-qLJE5xOk`, obj)
            .then(data => {
                const { responseType } = data.data;
                if (responseType == 'success') {
                    let listRelatedData = data.data.data;
                    listRelatedData = listRelatedData.ConceptClusters;
                    dispatch({
                        type: "GET_LIST_RELATED",
                        payload: listRelatedData
                    })
                }
                else {
                    dispatch({
                        type: "LIST_RELATED_SERVER_ERROR"
                    })
                }
            }).catch(error => {
                dispatch({
                    type: 'LIST_RELATED_NETWORK_ERROR'
                })
                console.log('Error While getting list related data', error);
            })
    }

}
