import React, { Component } from 'react';
import Loader from '../LoaderComponent/Loader';
import AdSense from 'react-adsense';
import "./ConceptsPage.css";
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { withRouter } from 'react-router-dom'
import ReactLoading from 'react-loading';
import LoadingOverlay from 'react-loading-overlay';
import AdsComponent from '../AdComponent/AdComponent';
import {
    flushConceptState, getListRelatedByConceptId,
    getConceptFromApi,
    increasePerspectiveLike,
    decreasePerspectiveLike,
    sortByKeyword,
    showAllPerspective,
    sortByAuthorLast,
    getConceptPerspectivesByAuthor,
    getConceptPerspectivesByKeywords,
    getAllPerspectivesByConcept,
    setConceptPageNo
} from "../../actions/conceptsActions";
import { setSelectedCategory, setSelectedItem, addTag } from "../../actions/navbaractions";
import { getAuthorGroups } from "../../utilities";
import Context from "./Context/Context";
import { getUniqueKeywords, getUniqueSortedAuthors } from '../../selectors';
import queryString from "query-string";
import Axios from 'axios';
class ConceptsPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            query: "",
            filters: false,
            apiLoading: this.props.concepts.apiLoading,
            apiServerError: this.props.concepts.apiServerError,
            apiNetworkError: this.props.concepts.apiNetworkError,
            conceptWithDetails: this.props.concepts.conceptWithDetails,
            listRelatedData: this.props.concepts.listRelatedData,
            listRelatedLoading: this.props.concepts.listRelatedLoading,
            listRelatedServerError: this.props.concepts.listRelatedServerError,
            listRelatedNetworkError: this.props.concepts.listRelatedNetworkError,
            selectedOption: this.props.navbar.selectedOption,
            perspectives: this.props.concepts.perspectives,
            keywords: this.props.concepts.keywords,
            concept: this.props.concepts.concept,
            authors: this.props.concepts.authors,
            perspectivesRelations: this.props.concepts.perspectivesRelations,
            perspectivesDetail: this.props.concepts.perspectivesDetail,
            UniqueKeywords: this.props.uniqueKeyWords,
            UniqueAuthors: this.props.uniqueAuthors,
            visibility: "hidden",
            keywordId: null,
            authorId: null,
            id: "",
            all: false,
            sortByAuthor: null,
            isLike: this.props.concepts.isLike,
            filteredPerspectives: [],
            perspectiveLoading: false,
            page_no: 1
        };
        this.getFilteredPerspectives = this.getFilteredPerspectives.bind(this);
        this.filterConceptDefinations = this.filterConceptDefinations.bind(this);
        this.onAuthorClick = this.onAuthorClick.bind(this);
        this.onListClick = this.onListClick.bind(this);
        this.onListRelatedClick = this.onListRelatedClick.bind(this);
        this.onRelatedClick = this.onRelatedClick.bind(this);
        this.toggleListRelated = this.toggleListRelated.bind(this);
        this.viewMorePerspectiveHandler = this.viewMorePerspectiveHandler.bind(this);
        this.onLikeClick = this.onLikeClick.bind(this);
        if (this.props.navbar.selectedOption) {
            if (this.props.navbar.selectedOption.id) {
                if (this.props.navbar.selectedOption.category == "Concepts") {
                    let concept = {
                        "concept_id": this.props.navbar.selectedOption.id,
                        "filters": this.props.navbar.tags.map(tag => tag.id),
                    };
                    this.props.getConceptFromApi(concept);
                    this.props.getListRelatedByConceptId(concept)
                    //this.props.setConceptPageNo(this.props.concepts.page_no + 1)
                }
            }
        }
    }
    onListRelatedClick(listRelated) {
        let label = listRelated.relationary_supra;
        let value = label;
        let id = listRelated.relationary_thing_supra_id;
        let category = "Concept-Clusters";
        this.props.flushConceptState()
        this.props.setSelectedItem({ label, value, id, category });
    }
    getConcept = (concept_name) => {
        return new Promise((resolve, reject) => {
            Axios.get(`${process.env.REACT_APP_SERVER}/api/rest/v1/concepts?name=${concept_name}`).then(data => {
                if (data.data.obj) {
                    let result = data.data.obj.selectedOption;
                    resolve(result);
                }
                resolve(null);
            }).catch(err => {
                console.log(err);
            });
        });
    }
    async componentWillMount() {
        if (this.props.match.params.name) {
            if (this.props.match.params.name && queryString.parse(this.props.location.search).filters) {
                let filters = [];
                let queryFilters = queryString.parse(this.props.location.search).filters;
                let concept_name = this.props.match.params.name;
                if (typeof queryFilters === "string") {
                    filters.push(queryFilters);
                } else {
                    filters = queryFilters;
                }
                this.setState({
                    query: concept_name,
                    filters: true
                })
                let result = await this.getConcept(concept_name);
                let author_group = await getAuthorGroups(filters);
                if (result) {
                    this.props.addTag(author_group);
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result,
                    });
                } else {
                    this.props.history.push('/Message');
                }

            } else {
                let concept_name = this.props.match.params.name;
                this.setState({
                    query: concept_name
                });
                let result = await this.getConcept(concept_name);
                if (result) {
                    this.props.setSelectedItem(result);
                    this.setState({
                        selectedOption: result,
                    })
                } else {
                    this.props.history.push('/Message');
                }
            }
        }

    }
    componentWillReceiveProps(nextProps) {
        if (this.state.selectedOption != nextProps.navbar.selectedOption) {
            this.props.flushConceptState();
            this.setState({
                apiLoading: nextProps.concepts.apiLoading,
                apiServerError: nextProps.concepts.apiServerError,
                apiNetworkError: nextProps.concepts.apiNetworkError,
                conceptWithDetails: nextProps.concepts.conceptWithDetails,
                conceptsWithDetails: nextProps.concepts.conceptsWithDetails,
                orignalConceptsWithDetails: nextProps.concepts.conceptsWithDetails,
                //perspectiveids: nextProps.concepts.perspectiveids,
                perspectivesRelations: nextProps.concepts.perspectivesRelations,
                perspectivesDetail: nextProps.concepts.perspectivesDetail,
                concept: nextProps.concepts.concept,
                authors: nextProps.concepts.authors,
                keywords: nextProps.concepts.keywords,
                UniqueKeywords: nextProps.uniqueKeyWords,
                UniqueAuthors: nextProps.uniqueAuthors,
                listRelatedData: nextProps.concepts.listRelatedData,
                listRelatedLoading: nextProps.concepts.listRelatedLoading,
                listRelatedNetworkError: nextProps.concepts.listRelatedNetworkError,
                listRelatedServerError: nextProps.concepts.listRelatedServerError,
                loading: nextProps.concepts.loading,
                //page_no: nextProps.concepts.page_no
            })
            if (nextProps.navbar.selectedOption.id && nextProps.navbar.selectedOption.category == "Concepts") {
                this.setState({
                    selectedOption: nextProps.navbar.selectedOption,
                });

                let concept = {
                    "concept_id": nextProps.navbar.selectedOption.id,
                    "filters": nextProps.navbar.tags.map(tag => tag.id)
                };
                this.props.getConceptFromApi(concept)
                this.props.getListRelatedByConceptId(concept);
            }
        }
        else {
            this.setState({
                apiLoading: nextProps.concepts.apiLoading,
                apiServerError: nextProps.concepts.apiServerError,
                apiNetworkError: nextProps.concepts.apiNetworkError,
                conceptWithDetails: nextProps.concepts.conceptWithDetails,
                conceptsWithDetails: nextProps.concepts.conceptsWithDetails,
                orignalConceptsWithDetails: nextProps.concepts.conceptsWithDetails,
                perspectiveids: nextProps.concepts.perspectiveids,
                keywords: nextProps.concepts.keywords,
                listRelatedData: nextProps.concepts.listRelatedData,
                listRelatedLoading: nextProps.concepts.listRelatedLoading,
                listRelatedNetworkError: nextProps.concepts.listRelatedNetworkError,
                listRelatedServerError: nextProps.concepts.listRelatedServerError,
                loading: nextProps.concepts.loading,
                perspectivesRelations: nextProps.concepts.perspectivesRelations,
                perspectivesDetail: nextProps.concepts.perspectivesDetail,
                concept: nextProps.concepts.concept,
                UniqueKeywords: nextProps.uniqueKeyWords,
                UniqueAuthors: nextProps.uniqueAuthors,
                authors: nextProps.concepts.authors,
                isLike: nextProps.concepts.isLike,
                // page_no: nextProps.concepts.page_no
            })
        }
        if (this.props.navbar.tags.length !== nextProps.navbar.tags.length) {
            if (nextProps.navbar.selectedOption.category == "Concepts") {
                let concept = {
                    "concept_id": this.state.selectedOption.id,
                    "filters": nextProps.navbar.tags.map(tag => tag.id)
                };
                this.props.getConceptFromApi(concept)
                this.props.getListRelatedByConceptId(concept);
            }

        }

        if (nextProps.concepts.perspectivesDetail !== this.state.perspectivesDetail) {

            this.setState({
                perspectiveLoading: false,
                filteredPerspectives: this.getFilteredPerspectives(
                    nextProps.concepts.perspectivesDetail,
                    nextProps.concepts.perspectivesRelations,
                    nextProps.concepts.authors,
                )
            })
            setTimeout(() => {
                this.setState({
                    perspectiveLoading: false,
                })
            }, 500)
        }
    }
    listenToScroll =() => {
        
        console.log("hellow");
    }
    componentDidMount() {
        window.addEventListener('scroll', this.listenToScroll)
        this.setState({
            filteredPerspectives: this.getFilteredPerspectives(
                this.state.perspectivesDetail,
                this.state.perspectivesRelations,
                this.state.authors,
            )
        })
    }
    componentDidUpdate() {
        if (this.state.sortByAuthor) {
            const element = document.getElementById(this.state.sortByAuthor);
            if (element) {
                element.scrollIntoView({ behavior: 'smooth' });
            }
        }
    }
    toggleListRelated() {
        this.setState({
            listRelated: !this.state.listRelated,
        })
    }
    onListClick() {
        this.setState({
            listRelatedRole: 'list'
        })
        this.toggleListRelated();
    }
    onRelatedClick() {
        this.setState({
            listRelatedRole: 'related',
        })
        this.toggleListRelated();
    }
    onLikeClick(id) {
        if (this.state.perspectives[id].isLike) {
            this.props.decreasePerspectiveLike(id);
        }
        else {
            this.props.increasePerspectiveLike(id);
        }
    }
    onAuthorClick(author) {
        let label = author.author_first + " " + author.author_last;
        let value = label;
        let id = author.auth_id;
        let born = author.born;
        let died = author.died;
        let category = "Authors"
        this.props.flushConceptState()
        this.props.setSelectedItem({ label, value, id, born, died, category });
    }
    filterConceptDefinations(keyword_id) {
        let rtn = [];
        this.state.orignalConceptsWithDetails.forEach(element => {
            if (element.keywords_id == keyword_id) {
                rtn.push(element);
            }
        });
        this.setState({
            conceptsWithDetails: rtn,
        })
    }
    viewMorePerspectiveHandler() {
        this.props.getAllPerspectivesByConcept(this.state.selectedOption.id, this.state.page_no)
        this.setState({
            page_no: this.state.page_no + 1,
            perspectiveLoading: true
        })
    }
    getFilteredPerspectives(perspectivesDetail, perspectivesRelations, authors) {
        if (perspectivesRelations && authors && perspectivesDetail && perspectivesDetail.length > 0) {
            let result = [];
            if (this.state.sortBy == 'keyword') {
                let filteredPerspectives = [];
                let nonFilteredPerspectives = [];
                perspectivesDetail.forEach(perspective => {
                    let includes = false;
                    if (perspectivesRelations[perspective.id]) {
                        perspectivesRelations[perspective.id].Keywords.forEach(k => {
                            if (k == this.state.sortByKeywordId) {
                                perspective.Author = perspectivesRelations[perspective.id].Author;
                                perspective.Keywords = perspectivesRelations[perspective.id].Keywords;
                                includes = true;
                            }
                        });
                        includes === true ? filteredPerspectives.push(perspective) : nonFilteredPerspectives.push(perspective);
                    }
                });
                result = filteredPerspectives.sort(function (a, b) {
                    let authorALastName = authors[a.Author].lastName.toUpperCase();
                    let authorBLastName = authors[b.Author].lastName.toUpperCase();
                    return (authorALastName < authorBLastName) ? -1 : (authorALastName > authorBLastName) ? 1 : 0;
                });
                return result;
            }
            else {
                if (this.state.sortByAuthor) {
                    // let includes = false;
                    // perspectivesDetail.forEach(perspective => {
                    //     if (perspectivesRelations[perspective.id] && authors[perspectivesRelations[perspective.id].Author].lastName === this.state.sortByAuthor) {
                    //         includes = true;
                    //     }
                    // })
                    //if (includes) {
                    perspectivesDetail.forEach(perspective => {
                        if (perspectivesRelations[perspective.id]) {
                            perspective.Author = perspectivesRelations[perspective.id].Author;
                            perspective.Keywords = perspectivesRelations[perspective.id].Keywords;
                            result.push(perspective)
                        }
                    })
                    return result
                    // }
                    // this.setState({
                    //     perspectiveLoading: true
                    // })
                    // this.props.getConceptPerspectivesByAuthor(this.state.sortByAuthor);
                } else {
                    perspectivesDetail.forEach(perspective => {
                        if (perspectivesRelations[perspective.id]) {
                            perspective.Author = perspectivesRelations[perspective.id].Author;
                            perspective.Keywords = perspectivesRelations[perspective.id].Keywords;
                            result.push(perspective);
                        }
                    });
                    result = result.sort(function (a, b) {
                        let authorALastName = authors[a.Author].lastName.toUpperCase();
                        let authorBLastName = authors[b.Author].lastName.toUpperCase();
                        return (authorALastName < authorBLastName) ? -1 : (authorALastName > authorBLastName) ? 1 : 0;
                    })
                    return result;
                }
            }
        }
    }
    render() {
        return (
            <LoadingOverlay
                spinner={<Loader></Loader>}
                active={this.state.perspectiveLoading}
            >
                <div className="overflowhidden" style={{ overflow: "hidden" }}>
                    {(this.state.selectedOption.id && this.state.selectedOption.category == "Concepts" || this.state.query) && < div className="container-fluid">
                        {this.state.apiLoading && <div style={{ width: "100px", margin: "auto", marginTop: "30vh" }}>
                            <ReactLoading type={"balls"} color={"red"} />
                        </div>}
                        {!this.state.apiLoading && !this.state.apiNetworkError && !this.state.apiServerError && <div className="row">
                            <div className="col-xs-12 col-sm-12 col-md-3  " style={{ borderRadius: "3px", backgroundColor: "#F6F0EF", textTransform: 'capitalize', height: "90vh", overflow: "auto", textAlign: "" }}>
                                <div className='' style={{ width: "100%", margin: "auto", background: "#F6F0EF", textAlign: '', borderRadius: '20px' }}>
                                    {this.state.listRelatedLoading && <div style={{ width: "100px", margin: "auto" }}>
                                        <ReactLoading type="balls" color="red" />
                                    </div>}

                                    {!this.state.listRelatedLoading && <div>
                                        <div style={{ backgroundColor: 'white', textAlign: 'center', marginTop: '10px', padding: '5px' }}><h3>{this.state.selectedOption.label}</h3></div>
                                    </div>}
                                    <div style={{ backgroundColor: 'white', marginBottom: '5px' }}>
                                        {!this.state.listRelatedLoading && <div style={{ paddingLeft: '50px', marginTop: '5px', background: 'white' }}>
                                            <label style={{ backgroundColor: "", borderRadius: "15px", border: '1px solid black', padding: "5px 20px", marginTop: "10px", marginLeft: "", marginRight: "5px", cursor: "pointer", }}
                                                onClick={this.onListClick}>Lists</label>
                                            <label style={{ cursor: "pointer", backgroundColor: "", borderRadius: "15px", border: '1px solid black', padding: "5px 15px", marginTop: "10px", marginLeft: "5px" }}
                                                onClick={this.onRelatedClick}>Related</label>
                                        </div>}
                                        <div style={{ width: '200px', border: "1px solid green", display: [this.state.listRelated ? 'block' : 'none'], textAlign: "center" }}>
                                            {this.state.listRelatedRole == 'list' && this.state.listRelatedData && this.state.listRelatedData.length > 0 && returnOnlyListData(this.state.listRelatedData).map(data => {
                                                return <div style={{ color: "blue", cursor: "pointer" }} onClick={() => this.onListRelatedClick({ relationary_supra: data.name, relationary_thing_supra_id: data.id })}>{data.name}</div>
                                            })}
                                            {this.state.listRelatedRole == 'related' && this.state.listRelatedData && this.state.listRelatedData.length > 0 && returnOnlyRelationData(this.state.listRelatedData).map(data => {
                                                return <div style={{ color: "blue", cursor: "pointer" }} onClick={() => this.onListRelatedClick({ relationary_supra: data.name, relationary_thing_supra_id: data.id })} >{data.name}</div>
                                            })}
                                        </div>
                                        {/* <div onClick={() => this.props.showAllPerspective(true)}  className="btn btn-link text-center">Show All</div> */}
                                    </div>
                                    {this.state.UniqueKeywords && (this.state.UniqueKeywords.length > 0 && <div style={{ width: "100%", margin: "auto" }} >
                                        <div style={{ backgroundColor: 'white', width: '100%' }}>
                                            <div className="row">
                                                <div className="col-xs-2 col-sm-2 col-md-2">
                                                    <div onClick={() => {
                                                        this.props.showAllPerspective(this.state.selectedOption.id)
                                                    }
                                                    } className="btn btn-link text-center rotate"  ><b>Show All</b></div>
                                                </div>
                                                <div className="col-8 col-sm-8 col-md-8" style={{ backgroundColor: 'white' }}>
                                                    {this.state.UniqueKeywords.map(keyword => {
                                                        return <a href="#top" style={{ cursor: "pointer", textDecoration: "none", color: "black", display: "block" }} onClick={() => {
                                                            this.setState({
                                                                sortBy: "keyword",
                                                                sortByKeywordId: keyword.id,
                                                            }, () => {

                                                                this.props.getConceptPerspectivesByKeywords(this.state.sortByKeywordId)

                                                            })
                                                        }}><b style={{ color: 'black', fontWeight: '15px:', fontSize: '16px' }}>{keyword.name}</b><span style={{ color: 'gray' }}>{" (" + keyword.count + ")"}</span></a>

                                                    })}
                                                </div>
                                            </div>



                                        </div>
                                    </div>)}
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-12 col-md-4" style={{ borderRadius: "3px", backgroundColor: "#F6F0EF", height: "90vh", borderBottom: "3px solid pink", overflow: "auto" }}>
                                <div id="main-content" style={{ backgroundColor: "#F6F0EF" }}>
                                    <div id="img" style={{ width: "398px", height: "280px", backgroundColor: "red" }}>
                                        <h4 style={{ zIndex: "3", padding: "10px", color: "white" }} className="pull-right">{this.state.selectedOption ? this.state.selectedOption.label : ""}</h4>
                                    </div>
                                    <div style={{ marginTop: "10px", border: "1px solid white", paddingTop: "5px", borderRadius: "5px", width: "398px", textAlign: "center", backgroundColor: "white" }}>
                                        <label className="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Positive
                                        </label>
                                        <label className="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Critical
                                        </label>
                                        <label className="checkbox-inline" style={{ marginRight: "10px" }}>
                                            <input type="checkbox" value="" />Funny
                                        </label>
                                    </div>
                                    {
                                        <div id="top"></div>
                                    }
                                    {
                                        this.state.selectedOption && this.state.filteredPerspectives && this.state.filteredPerspectives.length > 0 && this.state.filteredPerspectives.map(data => {
                                            return (
                                                <div>
                                                    <Context data={data}
                                                        authors={this.state.authors}
                                                        selectedOption={this.state.selectedOption}
                                                        keywords={this.state.keywords}
                                                        onAuthorClick={this.onAuthorClick}
                                                        onLikeClick={this.onLikeClick}
                                                        isLike={this.state.isLike}
                                                    />
                                                </div>
                                            );
                                        })
                                    }
                                    <div className="row" >
                                        <div className="col-md-7 col-sm-7 col-xs-7 text-left ">
                                            <span onClick={() => { this.viewMorePerspectiveHandler() }} style={{cursor:'pointer'}}><b>view more..</b></span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className="col-xs-12 col-sm-12 col-md-2" style={{ borderRadius: "3px", backgroundColor: "white", height: "90vh", overflow: "auto", border: "1px solid #a5a5a5", borderLeft: '1px solid black' }}>
                                {this.state.UniqueAuthors && this.state.UniqueAuthors.length > 0 && this.state.UniqueAuthors.map(data => {
                                    return (
                                        <div style={{ width: "150px", margin: "auto" }}><a onClick={() => {
                                            this.setState({
                                                sortByAuthor: data.lastName,
                                                sortBy: "",
                                                perspectiveLoading: true
                                            }, () => {
                                                this.props.getConceptPerspectivesByAuthor(this.state.sortByAuthor)

                                            })
                                        }} style={{ color: "black", textDecoration: "none", cursor: "pointer" }} href={`#${data.lastName}`}><b>{(data.lastName && data.firstName) ? (data.lastName + ", " + data.firstName) : (data.lastName ? data.lastName : (data.firstName ? data.firstName : ""))}</b></a></div>
                                    );
                                })}
                            </div>
                            <div className="col-xs-12 col-sm-12 col-md-3" style={{ borderRadius: "3px", backgroundColor: "white", height: "90vh", overflow: "auto", borderLeft: "0px", borderRight: "3px  solid pink", borderTop: "3px  solid pink", borderBottom: "3px  solid pink", textAlign: "center" }}>
                                <div className="text-center">
                                    <AdsComponent></AdsComponent>
                                </div>
                            </div>
                        </div>
                        }
                    </div>}
                </div>
            </LoadingOverlay>
        );
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        //getConceptDetails: bindActionCreators(getConceptDetails, dispatch),
        setSelectedCategory: bindActionCreators(setSelectedCategory, dispatch),
        setSelectedItem: bindActionCreators(setSelectedItem, dispatch),
        flushConceptState: bindActionCreators(flushConceptState, dispatch),
        getListRelatedByConceptId: bindActionCreators(getListRelatedByConceptId, dispatch),
        getConceptFromApi: bindActionCreators(getConceptFromApi, dispatch),
        addTag: bindActionCreators(addTag, dispatch),
        increasePerspectiveLike: bindActionCreators(increasePerspectiveLike, dispatch),
        decreasePerspectiveLike: bindActionCreators(decreasePerspectiveLike, dispatch),
        sortByAuthorLast: bindActionCreators(sortByAuthorLast, dispatch),
        sortByKeyword: bindActionCreators(sortByKeyword, dispatch),
        showAllPerspective: bindActionCreators(showAllPerspective, dispatch),
        getConceptPerspectivesByAuthor: bindActionCreators(getConceptPerspectivesByAuthor, dispatch),
        getConceptPerspectivesByKeywords: bindActionCreators(getConceptPerspectivesByKeywords, dispatch),
        getAllPerspectivesByConcept: bindActionCreators(getAllPerspectivesByConcept, dispatch),
        setConceptPageNo: bindActionCreators(setConceptPageNo, dispatch)

    }
}
const mapStateToProps = (state) => {
    return {
        navbar: state.navbar,
        concepts: state.concepts,
        pageNumber: state.concepts.page_no,
        uniqueKeyWords: getUniqueKeywords(state),
        uniqueAuthors: getUniqueSortedAuthors(state)
    }
}
// const renderConceptsDetails = (data) => {
//     if (data.length > 0) {
//         data.map(item => {
//             return (<h1>{item.description}</h1>);
//         })
//     }
// }
const returnOnlyListData = (listRelatedData) => {
    let listData = [];
    listRelatedData.forEach(element => {
        if (element.type == 'list of') {
            listData.push(element);
        }
    })
    return listData;
}
const returnOnlyRelationData = (listRelatedData) => {
    let listData = [];
    listRelatedData.forEach(element => {
        if (element.type == 'dictionary of') {
            listData.push(element);
        }
    })
    return listData;
}
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(ConceptsPage));